import React, {useState} from 'react'
import { Radio } from 'antd';
import "./SchoolType.css"

const SchoolType = (props) => {

    const onChange = ({ target: { value } }) => {
        props.setValue(value);
    };

    const onChangeType = ({ target: { value } }) => {
        props.setType(value);
    };

    const options = [
        {
          label: 'WG',
          value: 'WG',
        },
        {
          label: 'TG',
          value: 'TG',
        },
        {
          label: 'SG',
          value: 'SG',
        },
        {
            label: 'EG',
            value: 'EG',
        },
        {
            label: 'BTG',
            value: 'BTG',
        },
        {
            label: 'AG',
            value: 'AG',
        },
      ];

  return (
    <div className='schoolTypeWrapper'>
        <h1>Auf was fr eine Schule gehst du?</h1>
        
        <Radio.Group onChange={onChange} defaultValue={props.value} buttonStyle="solid">
            <Radio.Button className='radioButton' value="a">Allgemeinbildendes Gymnasium</Radio.Button>
            <Radio.Button className='radioButton' value="b">Berufliches Gymnasium</Radio.Button>
        </Radio.Group>

        {
            props.value == "b" && (
                <div className='typeWrapper'>
                    <p>Welches berufliche Gymnasium besuchst du?</p>
                   <Radio.Group
                        options={options}
                        defaultValue={props.type}
                        onChange={onChangeType}
                        optionType="button"
                        buttonStyle="solid"
                    /> 
                </div>
            )
        }
    </div>
  )
}

export default SchoolType