import React, {useState} from 'react';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import "./CourseElectionPage.css"
import { InfoOutlined} from '@ant-design/icons';
import { Alert, Space } from 'antd';


  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
  
    return result;
  };

  const grid = 8;



const CourseElectionPage = (props) => {
    const [data, setData] = useState({
        '1': props.list.filter((element) => element.sector === 1),
        '2': props.list.filter((element) => element.sector === 2),
        '3': props.list.filter((element) => element.sector === 3),
        '4': props.list.filter((element) => element.sector === 4),
        '5': [],
        '6': [],
        '7': [],
        '8': [],
    });
    const [draggedItem, setDraggedItem] = useState(null);
    const [error, setError] = useState(false);
  
    function onDragEnd(result) {
        console.log(result);

        setDraggedItem(null);
        // dropped outside the list
        if (!result.destination) {
            setError(true);

            setTimeout(() => setError(false), 5000); 
            return;
        }

        const start = data[result.source.droppableId.toString()];
        const finish = data[result.destination.droppableId.toString()];

        if(start === finish) {
            const newItems = reorder(
            data[result.source.droppableId.toString()],
            result.source.index,
            result.destination.index
            );
        
            setData((prev) => {
                prev[result.source.droppableId.toString()] = newItems;
                return prev;
            });
        } else {
            console.log(result.draggableId);
            console.log(result.destination.index)

            const removed = start[result.source.index];
            start.splice(result.source.index, 1)
            finish.splice(result.destination.index, 0, removed);

            setData((prev) => {
                prev[result.source.droppableId.toString()] = start;
                prev[result.destination.droppableId.toString()] = finish;
                return prev;
            });

        }
      }
    
    function onDragStart(start) {
        setDraggedItem(item(start.draggableId));
    }

    const item = name => props.list.find((element) => element.name === name);

    const calculateCourses = () => {
        let sum = 0;

        for (let index = 5; index <= 8; index++) {
            const element = data[index.toString()];

            element.forEach(element2 => {
                sum = index === 6 ? sum + 4 : sum + (element2.semester);
            });
            
        }

        return sum;
    }
    const calculateHours = () => {
        let sum = 0;

        for (let index = 5; index <= 8; index++) {
            const element = data[index.toString()];

            element.forEach(element2 => {
                sum = index === 5 ? sum + 20 : sum + (element2.hours * (index === 6 ? 4 : element2.semester));
            });
            
        }

        return (sum/4);
    }

    function getStyle(style, snapshot) {
        if (!snapshot.isDropAnimating) {
          return style;
        }
        return {
          ...style,
          // cannot be 0, but make it super tiny
          transitionDuration: `0.001s`,
        };
      }

  return (
    <>
    <DragDropContext onDragStart={onDragStart} onDragEnd={onDragEnd}>
        <div className='outerWrapper'>
            <div className='rightWrapper'>

                    <p className='listTitle'>Bereich I:</p>

                    <Droppable droppableId="1" isDropDisabled={draggedItem == null || draggedItem.sector != 1}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className='listWrapper'
                            >
                            {data['1'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCard'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>

                    <p className='listTitle'>Bereich II:</p>

                    <Droppable droppableId="2" isDropDisabled={draggedItem == null || draggedItem.sector != 2}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className='listWrapper'
                            >
                            {data['2'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCard'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>

                    <p className='listTitle'>Bereich III:</p>

                    <Droppable droppableId="3" isDropDisabled={draggedItem == null || draggedItem.sector != 3}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className='listWrapper'
                            >
                            {data['3'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCard'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>

                    <p className='listTitle'>Bereich Sport:</p>

                    <Droppable droppableId="4" isDropDisabled={draggedItem == null || draggedItem.sector != 4}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className='listWrapper'
                            >
                            {data['4'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCard'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                
            </div>

            <div className='leftWrapper'>
                <div>
                <div className='auswahlWrapper'>
                <p className='listTitle'>Leitungsfcher</p>
                <Droppable droppableId="5" isDropDisabled={draggedItem == null || !draggedItem.subjectTypes.includes("LF") || data['5'].length >= 3}>
                    {(provided, snapshot) => (
                        <div
                        {...provided.droppableProps}
                        ref={provided.innerRef}
                        className='auswahl'
                        style={
                            {
                                backgroundColor: (snapshot.isDraggingOver ? "rgba(25, 217, 15, .5)" : "transparent")
                            }
                        }
                        >
                        {data['5'].map((item, index) => (
                            <Draggable key={item.name} draggableId={item.name} index={index}>
                                {(provided, snapshot) => (
                                    <div
                                    ref={provided.innerRef}
                                    {...provided.draggableProps}
                                    {...provided.dragHandleProps}
                                    className='droppableSubjectCardAuswahl'
                                    >
                                    {item.name}
                                    </div>
                                )}
                            </Draggable>
                        ))}
                        {provided.placeholder}
                        </div>
                    )}
                </Droppable>
                </div>

                <div className='auswahlWrapper'>
                    <p className='listTitle'>Mndliche Prfungsfcher</p>
                    <Droppable droppableId="6" isDropDisabled={draggedItem == null || !draggedItem.subjectTypes.includes("BF") || data['6'].length >= 2}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className='auswahl'
                            style={
                                {
                                    backgroundColor: (snapshot.isDraggingOver ? "rgba(25, 217, 15, .5)" : "transparent")
                                }
                            }
                            >
                            {data['6'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCardAuswahl oral'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </div>

                <div className='auswahlWrapper'>
                    <p className='listTitle'>Basisfcher</p>
                    <Droppable droppableId="7" isDropDisabled={draggedItem == null || !draggedItem.subjectTypes.includes("BF")}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className='auswahl'
                            style={
                                {
                                    backgroundColor: (snapshot.isDraggingOver ? "rgba(25, 217, 15, .5)" : "transparent")
                                }
                            }
                            >
                            {data['7'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCardAuswahl basis'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </div>

                <div className='auswahlWrapper'>
                    <p className='listTitle'>Wahlfcher</p>
                    <Droppable droppableId="8" isDropDisabled={draggedItem == null || !draggedItem.subjectTypes.includes("WF")}>
                        {(provided, snapshot) => (
                            <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            isDraggingOver={snapshot.isDraggingOver}
                            className='auswahl'
                            style={
                                {
                                    backgroundColor: (snapshot.isDraggingOver ? "rgba(25, 217, 15, .5)" : "transparent")
                                }
                            }
                            >
                            {data['8'].map((item, index) => (
                                <Draggable key={item.name} draggableId={item.name} index={index}>
                                    {(provided, snapshot) => (
                                        <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        className='droppableSubjectCardAuswahl choose'
                                        >
                                        {item.name}
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </div>

                <div className='infoContainer'>
                        <InfoOutlined className='infoIcon'/>
                        <div>
                        <p><b>Anzahl der Kurse:</b> {calculateCourses()}</p>
                        <p><b> Anzahl der Wochenstunden:</b> {calculateHours()}</p>
                        </div>
                </div>
                </div>
            </div>

        </div>
      </DragDropContext>

    {error === true && (

        <Space className='alertSpace' direction="vertical">
    <Alert className='alert' type="error" message="Du hast das Fach in einen ungltigen Wahlbereich gezogen!" />
  </Space>
    )}

</>
  );
  


}

export default CourseElectionPage