import React from 'react'
import "./WFPage.css"
import { useState } from 'react';
import Icon, { MinusOutlined} from '@ant-design/icons';
import { Button } from 'antd';



const WFPage = (props) => {

  return (
    <>
        <h2 className='sectionTitle'>Entferne die Fcher, die du nicht whlen kannst!</h2>
        <div className='subjectCardWrapper'>
            
            {props.choosables
            .map((element, i) => {       
            return (
                <div className='subjectCard'>
                    <p>{element.name}</p>
                    <Button className='discardSubjectButton' onClick={() => {
                        props.setCoosables(props.choosables.filter(element2 => element2 != element))
                    }}>
                        <MinusOutlined  className='discardSubjectIcon'/>
                    </Button>
                </div>
            ) 
            })}
        </div>
    </>
  )
}

export default WFPage