import { Radio } from 'antd'
import React from 'react'
import { useState } from 'react';
import "./StartPage.css";
import { Button, message, Steps, theme} from 'antd';
import Icon, { ArrowRightOutlined, ArrowLeftOutlined, BookOutlined, QuestionOutlined, InfoOutlined } from '@ant-design/icons';
import SchoolType from '../SchoolType';
import WFPage from '../WFPage';
import subjects from "../../data/Subject"
import CourseElectionPage from '../CourseElectionPage';

function StartPage() {
  const [value, setValue] = useState('a');
  const [type, setType] = useState('WG');
  const [choosables, setCoosables] = useState(subjects.filter(element => (element.subjectTypes.includes("WF") || element.subjectTypes.includes("UT"))));


  const { token } = theme.useToken();
  const [current, setCurrent] = useState(0);

  const courseList = subjects.filter(element => (!element.subjectTypes.includes("WF") && !element.subjectTypes.includes("UT")));

  courseList.push(...choosables)

  const next = () => {
    setCurrent(current + 1);
  };
  const prev = () => {
    setCurrent(current - 1);
  };
  const steps = [
    {
      title: 'Schulform',
      content: <SchoolType setValue={setValue} setType={setType} value={value} type={type}/>,
      icon: <BookOutlined />
    },
    {
      title: 'Wahlbereich',
      content: <WFPage setCoosables={setCoosables} choosables={choosables}/>,
      icon: <QuestionOutlined />
    },
    {
      title: 'Kurswahl',
      content: <CourseElectionPage list={courseList}/>,
      icon: <InfoOutlined />
    }, 
  ];


  const items = steps.map((item) => ({
    key: item.title,
    title: item.title,
    icon: item.icon,
  }));

  return (
    <div className='stepWrapper'>
      <Steps className='steps' direction='horizontal' current={current} items={items} />
      <div className='stepContent'>{steps[current].content}</div>
      <div
        className='buttonWrapper'
        style={{
          marginTop: 24,
        }}
      >
        {current > 0 && (
          <Button
            className='previousButton'
            onClick={() => prev()}
          >
            <ArrowLeftOutlined className='previousButtonArrow'/>
          </Button>
        )}

        {current == 1 && (
          <Button
            className='resetButton'
            onClick={() => setCoosables(subjects.filter(element => (element.subjectTypes.includes("WF") || element.subjectTypes.includes("UT"))))}
          >
          Auswahl zurcksetzen
        </Button>
        )}

      {current == 2 && (
          <Button
            className='resetButton'
          >
          Als PDF exportieren
        </Button>
        )}

        {current == 0 && (<div></div>)}

        {current < steps.length - 1 && (
          <Button className='nextButton' onClick={() => next()}>
            <ArrowRightOutlined className='nextButtonArrow'/>
          </Button>
        )}
        
      </div> 
    </div>
  );
}

export default StartPage